@import url(https://fonts.googleapis.com/css2?family=Akaya+Telivigala&display=swap);
/** @format */
.App {
  font-family: "Akaya Telivigala", cursive;
  text-align: center;
  margin: auto;
  background-color: #99e6ff;
}

.glass {
  border: solid 5px blueviolet;
  border-radius: 10%;
  width: 70%;
  margin: auto;
  padding: 30px;
  background-color: yellow;
}
.small {
  border: solid 3px #4dffff;
  background-color: #3dc8ff;
  background-image: url("https://www.transparenttextures.com/patterns/nice-snow.png");
  border-radius: 50%;
  width: 40px;
  height: 40px;
}
.larg {
  border: solid 3px #4dffff;
  background-color: #0081bd;
  background-image: url("https://www.transparenttextures.com/patterns/asfalt-light.png");
  border-radius: 50%;
  width: 70px;
  height: 70px;
  margin: 10px;
}
img {
  height: 100px;
  width: 100px;
}

.small-up-div {
  padding: 3vw;
  display: grid;
  grid-template-columns: auto auto auto auto auto auto;
}
h1 {
  font-size: 20vh;
  color: #6600ff;
}
@media (max-width: 768px) {
  body {
    display: grid;
    grid-template-columns: auto;
  }
}

/** @format */

.animal {
  border-radius: 50%;
  padding: 10px;
  margin: 15px;
  background-color: yellowgreen;
}

/** @format */

.white {
  background-color: whitesmoke;
  height: 60vh;
  width: 5vw;
}
.black {
  background-color: #333;
  height: 40vh;
  width: 5vw;
  position: relative;
  bottom: 10vh;
  color: whitesmoke;
}
.piano {
  margin: 15px;
  padding: 30px;
  border: solid 5px;
  border-radius: 15%;
  background-color: whitesmoke;
}
.piano-btn {
  border-radius: 50%;
  padding: 10px;
  margin: 15px;
  background-color: peachpuff;
}
.piano-img {
  border-radius: 50%;
}

/** @format */

.songs-btn {
  border-radius: 50%;
  padding: 10px;
  margin: 15px;
  background-color: #ff66cc;
  font-family: "Akaya Telivigala", cursive;
}

/** @format */

.vehicles-btn {
  border-radius: 50%;
  padding: 10px;
  margin: 15px;
  background-color: #3a7bdd;
}

